<template>
  <div>
    <div ref="tableBox" class="table-container">
      <el-table :data="tableData" :height="height" style="width: 100%">
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column prop="name" label="" width="180"></el-table-column>
        <el-table-column prop="age" label="" width="180"></el-table-column>
        <el-table-column prop="address" label=""></el-table-column>
        <el-table-column prop="email" label=""></el-table-column>
        <el-table-column prop="updateTime" label=""></el-table-column>
      </el-table>
    </div>
    <el-pagination
      :page-sizes="[20, 50, 100]"
      :current-page="pageDesc.page"
      :page-size="pageDesc.size"
      @current-change="getTableData"
      @size-change="handleSizeChange"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total" background>
    </el-pagination>
  </div>
</template>

<script>
import API from '@/api'

export default {
  data () {
    return {
      height: null,
      tableData: [],
      pageDesc: {
        page: 1,
        size: 20
      },
      total: 0
    }
  },
  methods: {
    async getTableData (page = 1) {
      // this.tableData = []
      this.pageDesc.page = page
      let res = await API.getListData(this.pageDesc)
      console.log(res)
      this.tableData = res.list
      this.total = res.total
    },
    handleSizeChange (size) {
      this.pageDesc.size = size
      this.getTableData()
    }
  },
  mounted () {
    this.height = this.$refs.tableBox.clientHeight
  },
  created () {
    this.getTableData()
  }
}
</script>

<style lang="less">
.table-container {
  width: 100%;
  height: calc(100% - 50px);
}
.el-pagination {
  padding: 11px 16px;
  text-align: right;
}
</style>
