// axios
import axios from 'axios'
import qs from 'qs'
import Utils from './utils'

// axios.defaults.timeout = 30000
// axios.defaults.headers.common['Content-Type'] = 'application/json;charset=UTF-8'
const service = axios.create({
  // baseURL: process.env.BASE_API, // apibase_url
  timeout: 20000 // 
})

// http request 
service.interceptors.request.use(
  config => {
    // config.headers['X-TOKEN'] = Cache.getToken()
    if (!config.params) { config.params = {} }

    // noLoading => loading   stopCloseLoading => loading
    config.autoCloseLoading = !config.params.noLoading && !config.params.stopCloseLoading
    if (!config.params.noLoading) { Utils.openLoading() }
    delete config.params.noLoading

    // post needStringify => 
    if (config.method.toLocaleLowerCase() === 'post') {
      if (config.params.needStringify) {
        delete config.params.needStringify
        config.data = qs.stringify(config.params)
      } else {
        config.data = config.params
      }
      config.params = {}
    }
    return config
  },
  err => {
    return Promise.reject(err)
  }
)

// http response 
service.interceptors.response.use(
  response => {
    if (response.config.autoCloseLoading) {
      Utils.closeLoading()
    }
    if (response.data.code !== 0) {
      let rejection = {
        config: response.config,
        status: response.data.code,
        message: response.data.message
      }
      return Promise.reject(rejection)
    }
    return Promise.resolve(response.data)
  },
  error => {
    if (error.response) {
      if (error.response.status === 404) {
        // alert('')
      } else if (error.response.status === 403) {
        alert('')
      } else if (error.response.status === 401) {
        alert('')
        // router.replace('/login')
      } else if (error.response.status === 500) {
        alert('')
      }
      return Promise.reject(error)
    } else {
      alert(error.message)
      return Promise.reject(error)
    }
  }
)

export default service
